@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&family=Source+Sans+Pro:wght@400;600;700&display=swap');

body{
    font-family: 'font-Poppins';
}

@layer base {
    #__next {
        @apply contents;
    }

    html {
        @apply h-full scroll-smooth antialiased;
    }

    body {
        @apply bg-gray-700
    }
}